<template>
  <div class="imformation">
    <van-nav-bar
      title="个人资料"
      left-text="返回"
      left-arrow
      @click-left="black"
    />
    <BgBox>
        <van-cell-group>
      <van-cell title="头像">
        <template #right-icon>
          <div>
            <div class="userImg">
                <img :src="userInfo.userImg" alt="">
                <div class="mask">
                    <van-uploader :after-read="afterRead" />
                </div>
            </div>
          </div>
        </template>
      </van-cell>
      <van-cell title="用户ID" :value="userInfo.userId" />
      <van-cell title="手机号" :value="userInfo.phone" />
      <van-cell title="昵称">
          <template #right-icon>
              <div>
                  <input type="text" v-model="userInfo.nickName" @blur="changeName">
              </div>
          </template>
      </van-cell>
      <van-cell title="简介">
           <template #label>
              <div>
                  <textarea type="text" v-model="userInfo.desc" @blur="changeDesc"></textarea>
              </div>
          </template> 
      </van-cell>
    </van-cell-group>
    </BgBox>
  
  </div>
</template>

<script>
  import BgBox from "../components/BgBox.vue";
  export default {
    name: "Imformation",
    components: {
      BgBox
    },
    data(){
        return {
            userInfo: {}
        }
    },
    created() {
      this.getUserInfo();
    },
    methods: {
      black() {
        this.$router.go(-1);
      },
      getUserInfo() {
        let tokenString = localStorage.getItem("token");

        if (!tokenString) {
          this.$toast("请先登录");
          return this.$router.push({ name: "Login" });
        }

        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
        });
        this.axios({
          method: "GET",
          url: "findAccountInfo",
          params: {
            appkey: this.appkey,
            tokenString,
          },
        }).then((res) => {
          this.$toast.clear();
          if (res.data.code == "B001") {
            this.userInfo =res.data.result[0];
            if( this.userInfo.desc == '' ){
              this.userInfo.desc = '这个人很懒，什么也没留下！'
            }
          }
        });
      },
      afterRead(file){
          let imgType = file.file.type.split('/')[1];
          let serverBase64Img = file.content.split(',')[1];

          let tokenString = localStorage.getItem("token");

          if (!tokenString) {
              this.$toast("请先登录");
              return this.$router.push({ name: "Login" });
          }

          this.$toast.loading({
              message: "加载中...",
              forbidClick: true,
              duration: 0,
          }); 
          this.axios({
              method: 'POST',
              url: '/updateAvatar',
              data: {
                  appkey: this.appkey,
                  tokenString,
                  imgType,
                  serverBase64Img,
              }
          }).then(res => {
              this.$toast.clear();
              
              
              if(res.data.code == 'H001'){
                  this.userInfo.userImg = res.data.userImg;
              }
          })
      },
      changeName(){
          let nickName = this.userInfo.nickName;
          let tokenString = localStorage.getItem("token");

          if (!tokenString) {
              this.$toast("请先登录");
              return this.$router.push({ name: "Login" });
          }

          this.$toast.loading({
              message: "加载中...",
              forbidClick: true,
              duration: 0,
          }); 
          this.axios({
              method: 'POST',
              url: '/updateNickName',
              data: {
                  appkey: this.appkey,
                  tokenString,
                  nickName,
              }
          }).then(res => {
              this.$toast.clear();
              if(res.data.code == 'C001'){
                  this.userInfo.nickName = res.data.nickName;
                  this.$toast(res.data.msg);
              }
          }).catch(err => {
              this.$toast.clear();
          })
      },
      changeDesc(){
          let desc = this.userInfo.desc;
          let tokenString = localStorage.getItem("token");

          if (!tokenString) {
              this.$toast("请先登录");
              return this.$router.push({ name: "Login" });
          }

          this.$toast.loading({
              message: "加载中...",
              forbidClick: true,
              duration: 0,
          }); 
          this.axios({
              method: 'POST',
              url: '/updateDesc',
              data: {
                  appkey: this.appkey,
                  tokenString,
                  desc,
              }
          }).then(res => {
              this.$toast.clear();
              if(res.data.code == 'D001'){
                  this.userInfo.desc = res.data.desc;
                  this.$toast(res.data.msg);
              }
          }).catch(err => {
              this.$toast.clear();
          })
      }
    },
  };
</script>

<style lang="less" scoped>
  .imformation{
    background-color: #efefef;
    padding-bottom: 200px;
    .van-cell-group{
      width: 95%;
      margin: 0 auto;
      background-color: #efefef;
    }
  }
  .userImg {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
    img{
        width: 100%;
        height: 50px;
    }
    .van-uploader{
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
    }
  }
  .van-cell {
      input{
          border: none;
          outline: none;
          text-align: right;
          color: #a49799;
      }
      textarea{
          width: 100%;
          height: 38px;
          border: none;
          outline: none;
      }
      &:nth-child(5){
        border-bottom-left-radius: 20px;
        border-bottom-right-radius: 20px;
      }
    
  }
</style>